<template>
    <div :class="[`${type}`]" v-if="data">
        <div :class="[`${type}__wrapper`]">
            <div :class="[`${type}__wrapper--img`]" ref="wrapper"></div>
        </div>
        <div :class="[`${type}__main`]">
            <h2>
                <router-link to="/article">
                    <span>{{data.nail}}</span>
                    {{data.title}}
                </router-link>
            </h2>
            <p>
                {{data.desc}}
            </p>
            <div :class="[`${type}__main--line`]"></div>
            <div :class="[`${type}__main--foot`]">
                <span><i class="fa fa-user"></i>{{data.author}}</span>
                <span><i class="fa fa-clock"></i>{{data.date}}</span>
                <span><i class="fa fa-comments"></i>{{data.comments == 0 ? '暂无评论' : data.comments + '条评论'}}</span>
                <span><i class="fa fa-fire"></i>{{data.fire}}℃</span>
                <span><i class="fa fa-bookmark"></i>{{data.category}}</span>
                <span><i class="fa fa-tags"></i>{{data.labels.join(' / ')}}</span>
            </div>
        </div>
        <div :class="[`${type}__content`]">
            <slot></slot>
        </div>
    </div>
</template>

<script>
    export default {
        name: "index",
        props:{
            type:{
                type: String,
                default: 'yu-panel',
                validator (value) {
                    return ['yu-panel',"yu-article-panel"].includes(value);
                },
            },
            data:{
                type: Object,
                default: null,
            }
        },
        async mounted(){
            this.loadImg()
        },
        methods:{
            loadImg(){
                if(this.data && this.data.wrapper && this.data.wrapper != ""){
                    var loadimg = new Image();
                    loadimg.src = this.data.wrapper;
                    let ref = this.$refs.wrapper
                    loadimg.onload = function(){
                        ref.style.backgroundImage = `url("${loadimg.src}")`
                    }
                }
            }
        }
    }
</script>