<template>
    <div class="yu-bg" ref="yuBg">
        <div id="canvas" :style="`background-image: url('${img}')`">
            <div id="shadow">
                <div id='spidersOne'></div>
                <div id='spidersTwo'></div>
                <div id='spidersThree'></div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "bg",
        async mounted(){
            this.loadImg()
        },
        methods:{
            loadImg(){
                var loadimg = new Image();
                loadimg.src = "http://m.qpic.cn/psb?/V114w9Of1yklki/npVHtEwXWpOlyXyKiwZFpWP4IIf.WccaQ*hpwrg*LC8!/b/dLYAAAAAAAAA&bo=hgXoA4YF6AMRFyA!&rf=viewer_4"
                let ref = this.$refs.yuBg
                loadimg.onload = function(){
                    ref.style.backgroundImage = `url("${loadimg.src}")`
                }
            }
        }
    }
</script>