<template>
  <div id="app">
    <vue-progress-bar></vue-progress-bar>
    <Bg></Bg>
    <Header></Header>
    <main>
      <div id="yu-left">
        <Aside></Aside>
      </div>
      <div id="yu-right">
        <router-view></router-view>
      </div>
    </main>
    <Footer></Footer>
    <ScrollTop></ScrollTop>
  </div>
</template>

<script>

  import ScrollTop from './components/ScrollTop/index';
  import Header from './components/Header/index';
  import Footer from './components/Footer/index';
  import Bg from './components/Bg/index';

  import Aside from './iview/Aside/index';

export default {
  name: 'app',
  components: {
    ScrollTop,
    Header,
    Footer,
    Aside,
    Bg
  },
  data() {
    return {
    }
  },
  created() {
    this.initProgress()
  },
  mounted() {
      this.$Progress.finish()
  },
  methods: {
    // 注册顶部进度条
    initProgress() {
      this.$Progress.start()
      this.$router.beforeEach((to, from, next) => {
        this.$Progress.start()
        next()
      })
      this.$router.afterEach(() => {
        this.$Progress.finish()
      })
    }
  }
}
</script>

